.table-of-contents ._toc-item {
  color: inherit;
  padding: 8px;
  padding-left: 1.3em;
}
.table-of-contents ._toc-item.H1 {
  font-size: 1.1em;
  line-height: 1.7;
  font-weight: 700;
  border-left: 0.25rem solid transparent;
}
.table-of-contents ._toc-item.H2 {
  margin-left: 1em;
  font-size: 0.95em;
  line-height: 1.4;
  font-weight: 400;
}
.table-of-contents ._toc-item.H3 {
  margin-left: 2em;
  font-size: 0.9em;
  line-height: 1.4;
  font-weight: 500;
  height: 0;
  padding-top: 0;
  padding-bottom: 0;
  transform: scaleY(0);
  transform-origin: top;
  transition: all ease-in-out 160ms;
}
.table-of-contents ._toc-item.H1._has-active-child, .table-of-contents ._toc-item.H1.is-active {
  border-left: #FFD465 0.25rem solid;
  border-left: var(--q-color-primary, #FFD465) 0.25rem solid;
}
.table-of-contents ._toc-item.H3._has-active-parent, .table-of-contents ._toc-item.H3._has-active-sibling, .table-of-contents ._toc-item.H3.is-active {
  height: unset;
  padding-top: 8px;
  padding-bottom: 8px;
  transform: scaleY(1);
}